import './TodoItemReorderWrapper.css';
import {Reorder, useDragControls} from "framer-motion";
import {useDispatch, useSelector} from "react-redux";
import {deleteTask} from "../../../../redux/reducers/tasks";
import {TodoItem} from "./TodoItem/TodoItem";

export const TodoItemReorderWrapper = ({item}) => {
  const controls = useDragControls();
  const dispatch = useDispatch();

  const isEditTaskBlock = useSelector(state => state.tasks.isEditTaskBlock);

  return (
    <Reorder.Item
      key={item.id}
      value={item}
      className='TaskItemWrapper'
      dragListener={isEditTaskBlock}
      dragControls={controls}
    >
      <div
        className='btnDeleteItem'
        onClick={() => dispatch(deleteTask(item.id))}
      ></div>
      <TodoItem
        controls={controls}
        item={item}
        isEditTaskBlock={isEditTaskBlock}
      />
    </Reorder.Item>
  );
};



