import './TodoBlock.css';
import {TodoItemReorderWrapper} from "./CreateTaskBlock/TodoItemReorderWrapper/TodoItemReorderWrapper";
import {Reorder} from "framer-motion";
import React, {useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {setTaskCards} from "../../redux/reducers/tasks";
import {CreateTaskBlock} from "./CreateTaskBlock/CreateTaskBlock";

export const TodoBlock = () => {

  const dispatch = useDispatch();

  const {taskCards, isCreateTask} = useSelector(state => state.tasks);

  const onSetPosition = state => dispatch(setTaskCards(state));

  return (
    <>
      <Reorder.Group
        axis="y"
        values={taskCards}
        onReorder={onSetPosition}
        className='Todo-Block'
      >
        {taskCards.length === 0 && <div> </div>}
        {taskCards.length !== 0 && taskCards.map(item => <TodoItemReorderWrapper key={item.id} item={item}/>)}
      </Reorder.Group>
      {isCreateTask && <CreateTaskBlock />}
    </>
  )
};